.sliders {
	a {
		text-decoration: none;
		color: inherit;
		cursor: pointer;
		&:hover {
			text-decoration: none;
			color: inherit; } } }
.first_slider_wrapper {
	position: relative;
	padding-top: 5px;
	max-width: 1900px;
	margin: 0 auto;
	max-height: 600px;
	overflow: hidden; }
.banner {
	position: absolute;
	top: 0;
	bottom: 0;
	width: 360px;
	margin-top: 5px;
	color: #fff;
	background-color: rgba(47, 64, 80, 0.7);
	z-index: 4;
	left: 10%;
	padding: 40px 40px;
	@media(min-width:768px) {
		width: 480px;
		padding: 60px 40px; }
	@media(min-width:992px) {
		padding: 90px 40px; }
	.banner_title {
		font-size: 30px;
		margin-bottom: 20px;
		@media(min-width:718px) {
			font-size: 36px; }
		@media(min-width:768px) {
			font-size: 46px;
			margin-bottom: 40px; } }
	.banner_center {
		font-size: 19px; }
	.my_button {
		margin: 50px 0;
		height: 35px;
		@media(min-width:1050px) {
			margin: 80px 0; }
		@media(min-width:1200px) {
			margin: 100px 0; } } }
.first_slider {
	img {
		width: 100%; } }
.second_slider_wrapper {
	position: relative;
	margin: 20px auto;
	width: 80%;
	img {
		width: 100%; }
	.slider_prev {
		position: absolute;
		top: 50%; }
	.second_slider_nav {
		img {
			width: 40px; }
		div {
			z-index: 9;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			cursor: pointer; } }
	#secondSliderNext {
		// transform: translate(-50%, -50%)
		right: 0; }
	#secondSliderPrev {
		// transform: translate(-50%, -50%)
		left: 0; } }
.third_slider_wrapper,
.fourth_slider_wrapper,
.fifth_slider_wrapper {
	padding: 40px 20px 0;
	margin: 0px auto;
	a:hover {
		color: $gray; }
	h2 {
		text-transform: uppercase;
		// margin-bottom: 20px
		.best_in {} }

	img {
		width: 100%; }
	.slider_item {
		background-color: #fff;
		.slider_item_title {
			text-transform: uppercase;
			margin: 20px 0; }
		.slider_item_price {
			font-size: 20px; } } }
.fifth_slider {
	margin-bottom: 40px; }
.about {
	padding: 30px 40px;
	background-image: url('../../img/bg/about_us.jpg');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	color: #fff;
	h2 {
		color: #fff;
		margin-bottom: 30px; }
	p {
		color: #fff;
		font-weight: 300; }
	@media(min-width:992px) {
		padding: 50px 80px; } }
