.myAccNav {
    margin: 10px 0;
    @media(min-width:768px) {
        margin: 10px 0 30px; } }
.nav_item {
    position: relative;
    text-align: center;
    width: 100%;
    height: 63px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #E7EAEB;
    margin-right: 5px;
    padding: 0 5px;
    &:hover {
        background-color: darken(#E7EAEB, 10%);
        color: inherit;
        cursor: pointer; }
    &.active {
        background-color: rgb(32, 55, 74,);
        color: #fff;
        cursor: pointer;
        .nav_item_name, .nav_item_num {
            color: #fff; }
        &::after {
            border-left-color: #fff;
            transform: rotate(90deg); }
        .nav_item_img_myprof {
            background-image: url('../../img/icons/myprofW.png'); }
        .nav_item_img_dashboard {
            background-image: url('../../img/icons/dashboardW.png'); }
        .nav_item_img_chatroom {
            background-image: url('../../img/icons/chatroomW.png'); }
        .nav_item_img_purchases {
            background-image: url('../../img/icons/purchasesW.png'); }
        .nav_item_img_like {
            background-image: url('../../img/icons/likeW.png'); } }
    @media(min-width:992px) {
        padding: 0 10px;
        flex-direction: row;
        justify-content: flex-start;
        height: 50px;
        &::after {
            position: absolute;
            content: '';
            width: 10px;
            height: 10px;
            right: 8px;
            top: 50%;
            margin-top: -5px;
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            border-left: 10px solid #8a999c; } } }
.nav_item_img_dashboard {
    background-image: url('../../img/icons/dashboard.png'); }
.nav_item_img_myprof {
    background-image: url('../../img/icons/myprof.png'); }
.nav_item_img_chatroom {
    background-image: url('../../img/icons/chatroom.png'); }
.nav_item_img_purchases {
    background-image: url('../../img/icons/purchases.png'); }
.nav_item_img_like {
    background-image: url('../../img/icons/like.png'); }
.nav_item_img_dashboard,
.nav_item_img_myprof,
.nav_item_img_chatroom,
.nav_item_img_purchases,
.nav_item_img_like {
    width: 26px;
    height: 26px;
    background-size: cover;
    @media(min-width:992px) {
        margin-right: 10px; } }
